export default {
	data(){
		return{
			title:'',
			content:'',
		}
	},
	onLoad:function(e){
		var that=this;
		console.log(e.newsid)
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				that.title=res.data.title;
				that.content=res.data.content
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
